<template>
  <div class="slider">
    <swiper
      :slidesPerView="1"
      :navigation="true"
      :breakpoints="{
        '320': {
          slidesPerView: 1,
        },
        '768': {
          slidesPerView: 2,
        },
        '1024': {
          slidesPerView: 4,
        },
        '1920': {
          slidesPerView: 5,
        },
      }"
      :loop="true"
      :autoplay="true"
      :modules="modules"
      class="mySlider"
    >
      <swiper-slide
        class="mySlider__slide"
        v-for="(item, index) in sliderItem"
        :key="index"
        ><img
          class="mySlider__slide-img"
          :src="require('@/assets/images/slider/' + item.img)"
      /></swiper-slide>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { EffectFade, Navigation, Autoplay } from "swiper";
export default {
  data() {
    return {
      sliderItem: [
        {
          img: "1.jpg",
        },
        {
          img: "2.jpg",
        },
        {
          img: "3.jpg",
        },
        {
          img: "4.jpg",
        },
        {
          img: "5.jpg",
        },
        {
          img: "6.jpg",
        },
        {
          img: "7.jpg",
        },
        {
          img: "8.jpg",
        },
        {
          img: "9.jpg",
        },
        {
          img: "10.jpg",
        },
      ],
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [EffectFade, Navigation, Autoplay],
    };
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.slider {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  height: 100%;
  .mySlider {
    width: 100%;
    @include adaptivHeightlg(572, 350);
    overflow: hidden;
    @media (max-width: 768px) {
      @include adaptivHeightmd(600, 500);
    }
    &__slide {
      &-img {
        @media (max-width: 768px) {
          // filter: brightness(0.9);
        }
      }
    }
    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transform: scale(1);
      transition: 0.5s;
      &:hover {
        transform: scale(1.05);
        transition: 0.5s;
        @media (max-width: 768px) {
          transform: none;
        }
      }
    }
    &-slide {
      overflow: hidden;
    }
    .swiper-button-next,
    .swiper-button-prev {
      top: 95%;
      @include adaptivWidthLg(112, 50);
      width: 100%;
      @include adaptivHeightlg(23, 50);
      @media (max-width: 768px) {
        transform: translate(0%, 50%);
        @include adaptivWidthmd(130, 70);
        width: 100%;
        @include adaptivHeightmd(28, 15);
      }
      &::after {
        background-image: url("../assets/images/icon/right.png");
        background-size: cover;
        display: inline-block;
        width: 100%;
        height: 100%;
        content: "";
      }
    }
    .swiper-button-prev {
      &::after {
        background-image: url("../assets/images/icon/left.png");
        background-size: cover;
        display: inline-block;
        width: 100%;
        height: 100%;
        content: "";
      }
    }
  }
}
</style>