<template>
  <div class="photoGallery" id="gallery">
    <div class="container">
      <div class="photoGallery-head">
        <h4 class="photoGallery-head-title">photo gallery</h4>
        <div class="photoGallery-head-line"></div>
        <p class="photoGallery-head-descr">
          Раскрой себя в атмосфере любимого фильма
        </p>
      </div>
    </div>
    <swiper
      :slidesPerView="1"
      :navigation="true"
      :breakpoints="{
        '320': {
          slidesPerView: 1,
        },
        '768': {
          slidesPerView: 2,
        },
        '1024': {
          slidesPerView: 2,
        },
        '1920': {
          slidesPerView: 4,
        },
      }"
      :loop="true"
      :autoplay="{
        delay: 3000,
        disableOnInteraction: false,
      }"
      :modules="modules"
      class="photoGallerySlider"
    >
      <swiper-slide
        class="photoGallerySlider__slide"
        v-for="(item, index) in photoGalleryItem"
        :key="index"
      >
        <img
          class="photoGallerySlider__slide-img"
          :src="require('@/assets/images/sliderPreview/gallery/' + item.img)"
        />
        <div class="photoGallerySlider__slide-name">
          <h5 class="photoGallerySlider__slide-name-title">{{ item.title }}</h5>
          <p class="photoGallerySlider__slide-name-descr">{{ item.descr }}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
  
  <script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { EffectFade, Navigation, Autoplay } from "swiper";
export default {
  data() {
    return {
      photoGalleryItem: [
        {
          img: "1.jpg",
          title: "Лолита",
        },
        {
          img: "2.jpg",
          title: "Троя",
        },
        {
          img: "3.jpg",
          title: "Близость",
        },
        {
          img: "4.jpg",
          title: "Матрица",
        },
        {
          img: "5.jpg",
          title: "Произведение Ремарка",
        },
        {
          img: "6.jpg",
          title: "50 Оттенков серого",
        },
      ],
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    return {
      modules: [EffectFade, Navigation, Autoplay],
    };
  },
  computed: {
    pageData() {
      return this.$store.getters.getPageData(this.$route.params.id);
    },
  },
};
</script>
  
  <style lang="scss">
@import "@/assets/scss/grid/grid";
.photoGallery {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  background: #fff;
  @include adaptivPaddinglg(0, 0, 60, 100, 0, 0, 30, 50);
  @media (max-width: 768px) {
    @include adaptivPaddingmd(0, 0, 60, 60, 0, 0, 40, 40);
  }
  &-head {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    &-title {
      @include adaptiv-fontlg(80, 40);
      @include adaptiv-lineHeightlg(80, 40);
      font-family: "Playfair Display", serif;
      font-weight: 400;
      color: #5c001f;
      @media (max-width: 768px) {
        @include adaptiv-fontmd(80, 40);
        @include adaptiv-lineHeightmd(80, 40);
      }
    }
    &-line {
      width: 70%;
      height: 1px;
      background: #5c001f;
      @include adaptivMarginlg(0, 0, 25, 25, 0, 0, 13, 13);
      display: flex;
      align-items: center;
      @media (max-width: 768px) {
        width: 90%;
        @include adaptivMarginmd(0, 0, 25, 25, 0, 0, 13, 13);
      }
      &::before {
        content: "";
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: #5c001f;
        display: block;
      }
      &::after {
        content: "";
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: #5c001f;
        display: block;
        margin-left: auto;
      }
    }
    &-descr {
      @include adaptiv-fontlg(25, 13);
      @include adaptiv-lineHeightlg(25, 13);
      font-family: "Helvetica-normal";
      color: #5a5a5a;
      @media (max-width: 768px) {
        @include adaptiv-fontmd(25, 13);
        @include adaptiv-lineHeightmd(25, 13);
      }
    }
  }
  .photoGallerySlider {
    width: 80%;
    @include adaptivMarginlg(0, 0, 50, 0, 0, 0, 25, 0);
    margin-left: auto;
    position: static;
    @media (max-width: 768px) {
      overflow: visible;
      @include adaptivMarginmd(0, 0, 50, 0, 0, 0, 25, 0);
    }
    &__slide {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-end;
      cursor: pointer;
      &-img {
        border-radius: 50%;
        transition: 0.5s;
      }
      &:hover {
        .photoGallerySlider__slide-img {
          filter: brightness(0.5);
          transition: 0.5s;
        }
        .photoGallerySlider__slide-name-title {
          color: #5c001f;
          transition: 0.5s;
        }
      }
      &-name {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        @include adaptivMarginlg(0, 0, 25, 0, 0, 0, 13, 0);

        &-title {
          @include adaptiv-fontlg(21, 15);
          @include adaptiv-lineHeightlg(25, 19);
          font-family: "Roboto", sans-serif;
          font-weight: 700;
          color: #202e28;
          @media (max-width: 768px) {
            @include adaptiv-fontmd(21, 15);
            @include adaptiv-lineHeightmd(25, 19);
          }
        }
        &-descr {
          @include adaptiv-fontlg(15, 13);
          @include adaptiv-lineHeightlg(22, 16);
          font-family: "Roboto", sans-serif;
          font-weight: 300;
          color: #202e28;
          @media (max-width: 768px) {
            @include adaptiv-fontmd(15, 13);
            @include adaptiv-lineHeightmd(22, 16);
          }
        }
      }
    }
    .swiper-wrapper {
      display: flex;
      align-items: center;
    }
    .swiper-button-next {
      display: none;
    }
    .swiper-button-prev {
      position: absolute;
      z-index: 10;
      @include adaptivWidthLg(75, 50);
      width: 100%;
      @include adaptivHeightlg(75, 50);
      transform: translate(180%, 50%);
      @media (max-width: 768px) {
        display: none;
      }
      &::after {
        background-image: url("../assets/images/icon/arrowLeft.png");
        background-size: cover;
        display: inline-block;
        width: 100%;
        height: 100%;
        content: "";
      }
    }
  }
}
</style>